function Steering({speed}) {
    var angle = speed.angle;
    var str = 'rotate(' + angle + 'deg)';
    const style = {
        transform: str
    }
    
    var turn;
    if (angle === 0){
        turn = "";
    } else if (angle < 0){
        turn = "Turning left";
    } else {
        turn = "Turning right";
    }

    return (
        <div className="Card1">
            <h4>STEERING</h4>
            <img src='data:image/png;base64,' 
                style={style}
            />
            <p1>{turn}</p1>
        </div>
    )
}

export default Steering
